import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { signOut } from 'firebase/auth';
import { clearAuth } from '../../redux/authSlice';

import styled, { keyframes } from 'styled-components';
import SignOutButton from '../../components/auth/google/SignOutButton';
import { auth } from '../../firebase/firebase';

const MenuPage = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const authData = useSelector((state) => state.auth.data);
  
  const handleLogout = async () => {
    await signOut(auth);
    dispatch(clearAuth());
  };

  return (
    <Wrapper>
      <InfoWrapper>
        <img src={authData?.photoURL} alt="" />
        <div className="user-info">
          <div className="name">: {authData?.name}</div>
          <div className="email">: {authData?.email}</div>
          <div className="highScore">: {authData?.highScore}</div>
        </div>
      </InfoWrapper>
      <MenuWrapper>
        <MenuButton
          bgc={'yellow'}
          fc={'tomato'}
          onClick={() => navigate('/game')}
        >
          
        </MenuButton>
        <MenuButton
          bgc={'yellow'}
          fc={'tomato'}
          onClick={() => navigate('/leaderboard')}
        >
          
        </MenuButton>
        <MenuButton
          bgc={'yellow'}
          fc={'tomato'}
          onClick={() => navigate('/credit')}
        >
          
        </MenuButton>
        <SignOutButton f={handleLogout} />
      </MenuWrapper>
    </Wrapper>
  );
};

const InfoWrapper = styled.div`
  border: 2px solid tomato;
  border-radius: 10px;
  padding: 15px 20px;

  width: 280px;
  background-color: rgba(255, 99, 71, 0.9);

  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 15px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);

  img {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    object-fit: cover;
    border: 2px solid white;
  }

  .user-info {
    display: flex;
    flex-direction: column;
    gap: 6px;
    color: white;
    font-weight: 500;

    .name {
      font-size: 1.1rem;
      font-weight: bold;
    }

    .email {
      font-size: 0.8rem;
      opacity: 0.9;
    }

    .highScore {
      font-size: 0.9rem;
      opacity: 0.9;
    }
  }
`;

const changeBackground = keyframes`
  0%   { background-color: #A18CD1; }
  25%  { background-color: #FBC2EB; }
  50%  { background-color: #FDCB6E; }
  75%  { background-color: #81ECEC; }
  100% { background-color: #A18CD1; }
`;

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;

  animation: ${changeBackground} 7.6s linear infinite;
  transition: background-color 0.2s ease;
  gap: 50px;
`;

const MenuWrapper = styled.div`
  border: 2px solid black;
  border-radius: 5px;

  padding: 30px;
  /* width: 250px;
  height: 140px; */

  background-color: black;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
`;

const MenuButton = styled.button`
  width: 200px;
  height: 50px;
  background-color: ${(prop) => prop.bgc};
  color: ${(prop) => prop.fc};
  font-size: 20px;
  font-weight: 700;

  &:hover {
    background-color: lightgray;
  }
`;

export default MenuPage;
