import { useCallback, useEffect, useRef, useState } from 'react';
import styled from 'styled-components';
import { GAME_DURATION } from '../../defines/Rule';
import TimeLeft from '../../components/TimeLeft';
import { useDispatch, useSelector } from 'react-redux';
import { ref, update } from 'firebase/database';
import { db } from '../../firebase/firebase';
import { setAuth } from '../../redux/authSlice';
import { useNavigate } from 'react-router-dom';

const WordDropGame = ({ WORDS }) => {
  const [words, setWords] = useState([]); //!<    state
  const [input, setInput] = useState(''); //!< input  state
  const [score, setScore] = useState(0); //!<  state
  const [gameOver, setGameOver] = useState(false); //!< GAME_DURATION   state
  const shapeId = useRef(0); //!<        id
  const scoreRef = useRef(score); //!< useCallback        
  const authData = useSelector((state) => state.auth.data);
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const handleRetry = () => {
    //   
    setScore(0);
    setWords([]);
    setGameOver(false);
  };

  //!<  
  useEffect(() => {
    if (gameOver) return;
    const interval = setInterval(() => {
      const newWord = {
        id: shapeId.current++, //!< ID  (   )
        text: WORDS[Math.floor(Math.random() * WORDS.length)], //!< WORD    
        left: Math.random() * 80, //!< %  
        top: 0, //  
        speed: 0.5 + Math.random() * 1.0, //!<    (vh per tick)
      };
      setWords((prev) => [...prev, newWord]);
    }, 1500);

    //!< effect clean-up : interval break
    return () => clearInterval(interval);
  }, [gameOver]);

  //   (top )
  useEffect(() => {
    if (gameOver) return;

    const fallInterval = setInterval(() => {
      setWords(
        (prev) =>
          prev
            .map((w) => ({ ...w, top: w.top + w.speed })) //!<  ( +  =>  )
            .filter((w) => w.top < 100) // 100vh  
      );
    }, 50);

    //!< effect clean-up: interval break
    return () => clearInterval(fallInterval);
  }, [gameOver]);

  const handleInputChange = (e) => {
    const val = e.target.value.trim().toLowerCase();
    setInput(val);
  };

  const onEnter = (e) => {
    e.preventDefault();

    //!<       (     )
    const matched = words.find((w) => w.text === input);

    if (matched) {
      setScore((prev) => prev + input.length); //1<    
      setWords((prev) => prev.filter((w) => w.id !== matched.id)); //!<   
    }
    e.target.reset();
    setInput('');
  };

  useEffect(() => {
    scoreRef.current = score;
  }, [score]);

  const handleTimeout = useCallback(() => {
    if (scoreRef.current > authData.highScore) {
      const updates = {};
      updates[`/leaderboard/${authData.uid}/score`] = scoreRef.current;
      updates[`/users/${authData.uid}/highScore`] = scoreRef.current;
      update(ref(db), updates)
        .then(() => {
          console.log('    !');
          dispatch(setAuth({ ...authData, highScore: scoreRef.current }));
        })
        .catch((error) => {
          console.error(' :', error);
        });
    }

    setGameOver(true);
  }, []);

  return (
    <GameWrapper>
      {!gameOver ? (
        <>
          <HighScore>: {authData.highScore.toLocaleString()}</HighScore>
          <CurrScore>: {score.toLocaleString()}</CurrScore>
          <TimeLeft duration={GAME_DURATION} onTimeout={handleTimeout} />
          {words.map((word) => (
            <Word key={word.id} left={word.left} top={word.top}>
              {word.text}
            </Word>
          ))}
          <form onSubmit={onEnter}>
            <InputBox
              type="text"
              value={input}
              onChange={handleInputChange}
              autoFocus
              placeholder=" "
              disabled={gameOver}
            />
          </form>
        </>
      ) : (
        <GameOverScreen>
          <GameOverMessage> !</GameOverMessage>
          <GameOverMessage> : {score}</GameOverMessage>
          <Button onClick={handleRetry}></Button>
          <Button onClick={() => navigate('/menu')}> </Button>
        </GameOverScreen>
      )}
    </GameWrapper>
  );
};

export default WordDropGame;

const GameWrapper = styled.div`
  width: 100vw;
  height: 100vh;
  background: #222;
  position: relative;
  overflow: hidden;
`;

const HighScore = styled.div`
  position: absolute;
  top: 15px;
  left: 15px;
  color: white;
  font-size: 1.5rem;
  z-index: 10;
`;

const CurrScore = styled.div`
  position: absolute;
  top: 50px;
  left: 15px;
  color: white;
  font-size: 1.5rem;
  z-index: 10;
`;

const Word = styled.div`
  position: absolute;
  top: ${(props) => props.top}vh;
  left: ${(props) => props.left}vw;
  color: #ffd700;
  font-weight: bold;
  font-size: 1.5rem;
  user-select: none;
`;

const InputBox = styled.input`
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  width: 50vw;
  max-width: 400px;
  padding: 10px 15px;
  font-size: 1.2rem;
  border-radius: 8px;
  border: none;
  outline: none;
`;

const GameOverScreen = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #222;
  color: white;
  font-size: 2rem;
  gap: 1rem; /*    */
`;

const Button = styled.button`
  background-color: yellow;
  border: none;
  padding: 0.5rem 1.5rem;
  font-size: 1.2rem;
  cursor: pointer;
  border-radius: 8px;
  margin-top: 0.5rem;
`;

const GameOverMessage = styled.div`
  color: white;
  font-size: 2rem;
  /* position: absolute; */
  /* top: 50%;
  left: 50%; */
  /* transform: translate(-50%, -50%); */
`;
