import './App.css';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import PrivateRoute from './redux/PrivateRoute';
import MenuPage from './pages/menu/MenuPage';
import CreditPage from './pages/credit/CreditPage';
import WordDropGame from './pages/game/WordDropGame';
import SignInPage from './pages/auth/google/SignInPage';
import LeaderBoard from './pages/leaderboard/leaderBoard';
import { db } from './firebase/firebase';
import { onValue, ref } from 'firebase/database';
import { setWords } from './redux/wordSlice';
import { useEffect } from 'react';

function App() {
  const dispatch = useDispatch();
  const wordData = useSelector((state) => state.words.data);
  useEffect(() => {
    const wordsRef = ref(db, 'WORDS');

    const unsubscribe = onValue(wordsRef, (snapshot) => {
      const rawWords = snapshot.val();
      if (rawWords) {
        dispatch(setWords(rawWords));
      }
    });
    return () => unsubscribe();
  }, []);

  return (
    <BrowserRouter basename={process.env.PUBLIC_URL}>
      <Routes>
        <Route path="*" element={<SignInPage />} />
        <Route
          path="/menu"
          element={
            <PrivateRoute>
              <MenuPage />
            </PrivateRoute>
          }
        />
        <Route
          path="/leaderboard"
          element={
            <PrivateRoute>
              <LeaderBoard />
            </PrivateRoute>
          }
        />
        <Route
          path="/credit"
          element={
            <PrivateRoute>
              <CreditPage WORDS={wordData} />
            </PrivateRoute>
          }
        />
        <Route
          path="/game"
          element={
            <PrivateRoute>
              <WordDropGame WORDS={wordData} />
            </PrivateRoute>
          }
        />
      </Routes>
    </BrowserRouter>
  );
}

export default App;
