import { setAuth } from '../../../redux/authSlice';
import { auth, provider, db } from '../../../firebase/firebase';
import { signInWithPopup } from 'firebase/auth';
import { ref, get, set } from 'firebase/database';
import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';

import styled from 'styled-components';

import SignInButton from '../../../components/auth/google/SignInButton';

const SignInPage = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const loginWithGoogle = async () => {
    try {
      const result = await signInWithPopup(auth, provider);
      const authUser = result.user;

      // DB    
      const userRef = ref(db, 'users/' + authUser.uid);
      const snapshot = await get(userRef);

      const raw2Auth = {
        uid: authUser.uid,
        name: authUser.displayName,
        email: authUser.email,
        photoURL: authUser.photoURL,
      };

      const leaderBoard = {
        name: authUser.displayName,
        photoURL: authUser.photoURL,
        score: 0
      };

      if (!snapshot.exists()) {
        raw2Auth.highScore = 0;
        raw2Auth.createdAt = new Date().toISOString();
        await set(userRef, raw2Auth );
        const userLeaderBoardRef = ref(db, 'leaderboard/' + authUser.uid);
        await set(userLeaderBoardRef, leaderBoard);

      } else {
        const data = snapshot.val();
        raw2Auth.createdAt = data.createdAt;
        raw2Auth.highScore = data.highScore;
      }

      dispatch(setAuth(raw2Auth));
      navigate('/menu');
    } catch (err) {
      console.error(' :', err);
    }
  };
  return (
    <Wrapper>
      <SignInButton f={loginWithGoogle} />
    </Wrapper>
  );
};

export default SignInPage;

const Wrapper = styled.div`
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;
