import { createSlice } from '@reduxjs/toolkit';

const initialState = {
  data: null,
};

const wordSlice = createSlice({
  name: 'word',
  initialState,
  reducers: {
    setWords: (state, action) => {
      state.data = action.payload;
    },
  },
});

export const { setWords } = wordSlice.actions;
export default wordSlice.reducer;
