import { useNavigate } from 'react-router-dom';
import styled, { keyframes } from 'styled-components';

const CreditPage = ({ WORDS }) => {
  const navigate = useNavigate();
  return (
    <Wrapper>
      <Credits onAnimationEnd={() => navigate('/menu')}>
        <p>Producer:  </p>
        <p>System Design:  </p>
        <p>Art Design:  </p>
        <p>Engineer:  </p>
        <p>  :</p>
        <p>useCallback() useEffect() useState() useRef() setInterval()</p>
        <p>:</p>
        <WordTable>
          <tbody>
            {WORDS.reduce((rows, word, index) => {
              if (index % 5 === 0) {
                rows.push([]);
              }
              rows[rows.length - 1].push(word);
              return rows;
            }, []).map((row, i) => (
              <tr key={i}>
                {row.map((word, j) => (
                  <td key={j}>{word}</td>
                ))}
              </tr>
            ))}
          </tbody>
        </WordTable>
        <br />
        <br />
        <p>A Dios</p>
        <p></p>
        <p>THE END</p>
      </Credits>
    </Wrapper>
  );
};

export default CreditPage;

const scrollUp = keyframes`
  0% {
    transform: translateY(65%);
  }
  100% {
    transform: translateY(-68%);
  }
`;

const Wrapper = styled.div`
  width: 100vw;
  height: 100vh;
  background-color: black;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const Credits = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 2rem;
  color: white;
  animation: ${scrollUp} 15s linear forwards;
  line-height: 2.5rem;
`;

const WordTable = styled.table`
  margin: 1rem 0;
  border-collapse: collapse;
  td {
    padding: 0.5rem 2rem;
    text-align: center;
  }
`;
